.recipescreen {
    max-width: 1300px;
    margin: 1rem auto;
    display: flex;
  }
  
  .recipescreen__left {
    display: flex;
    flex: 0.8;
  }
  
  .left__image {
    margin: 1rem;
    flex: 0.6;
  }
  
  .left__info {
    margin: 1rem;
    flex: 0.4;
    background: #fff;
    height: fit-content;
    font-size: 1.1rem;
  }
  
  .left__name {
    font-weight: bold;
    font-size: 1.5rem;
  }
  
  .left__info > p,
  .right__info > p {
    padding: 1rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
  
  .left__info > p:last-child,
  .right__info > p:last-child {
    border: none;
  }
  
  .recipescreen__right {
    flex: 0.2;
  }
  
  .right__info {
    width: 250px;
    margin: 1rem;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
  }
  
  .right__info p {
    padding: 1rem;
    font-size: 1rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .right__info p span {
    padding: 1rem;
  }
  
  .right__info p > button {
    grid-column: 1/-1;
    width: 100%;
    padding: 10px 16px;
    background: #171717;
    color: #f4f4f4;
    border: 1px solid #171717;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .right__info select {
    padding: 10px 16px;
  }
  
  @media (max-width: 960px) {
    .recipescreen {
      flex-direction: column;
    }
  
    .recipescreen__left {
      flex-direction: column;
      flex: 1;
    }
  
    .left__image {
      flex: 1;
    }
  
    .left__info {
      flex: 1;
    }
  
    .recipescreen__right {
      flex: 1;
      padding: 1rem;
    }
  
    .right__info {
      width: 100%;
      margin: 0;
    }
  }